    }
    toFixed2(num: number) {
        return num.toFixed(2);
    }
    sumScore(arr: any) {
        //console.log(arr);
        let numberArr = arr.length;
        let sumAvg = _.sumBy(arr, 'avg');
        let score = (sumAvg / arr.length);
        return score.toFixed(2);
    }

